/* Disable pinch-to-zoom functionality */

/* ::backdrop : #7c938d 
    redColor : #a80000
    black :#0f1014
    faintBlack : #575258;
    */

    @import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');
    @import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

html, body {
  touch-action: manipulation;
  overscroll-behavior: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden; /* Disable horizontal scrolling */
  user-select: none; /* Disable text selection */
  font-family: 'Roboto', sans-serif;
}

.app-container {
  max-width: 412px; /* Set width specific to OnePlus 7 Pro (1080x2340) */
  height: 100vh; /* Full viewport height */
  overflow-y: auto; /* Allow vertical scrolling */
  margin: 0 auto; /* Center container */
  padding: 0 0 30px 0;
  /* background-color: #f0f0f0; */
  /* background-color: #b7c7fc; */
  /* background-color: #adc4f5a3; */
  background-color: #cde7dd;
  position: relative;
}

.main-container {
  background-color: #ffffff;
  margin:auto 20px;
  margin-top: 80px;
  /* margin-bottom:40px; */
  padding: 0 0 100px 0; 
  border-radius:10px;
  color:#514f52;
}

.cross {
  position: absolute;
  top: 35px;
  left: 25px;
  font-weight: bold;
  text-decoration:none;
}

.all-tickets {
  position: relative;
  top: 36px;
  left: 290px;
  text-decoration: underline;
  font-size:15px;
}
.top-con {
  /* background-color: #fa050a; */
  background-color: #da0922;
  color: #f0f0f0;
 
  padding: 5px 10px;
  border-radius: 10px 10px 0 0;
  font-family: 'Roboto',sans-serif;
  text-align:center;
  font-size:20px;
  letter-spacing:0.2px;
}
.top-con p{
  margin: 0;
  font-size: 18px;
}

.details-con {
  /* padding: 5px 10px; */
  margin-top: 10px;
  display: flex;
  justify-content:space-around;
  align-items:center;
 
}
.value_1 {
  font-weight:900;
  font-size:20px;
  margin: 0;
  margin-top: 5px;
  text-align:center;
  color: rgb(45, 45, 45);
}
.title {
  font-family:'Inter', sans-serif;
  color: #575258;
  
  text-align:start;
  margin:0;
  margin-top: 10px;
  font-size: 13px;
}

.route-info {
  display: grid;
  padding: 15px 15px 0 15px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 70px;
  position: relative;
}

.route-name1 {
  text-align: center;
  padding: 0 5px;
  grid-column:1;
  display:flex;
  align-items:center;
  font-size:18px;
}

.route-name2 {
  text-align: center;
  padding: 0 5px;
  margin-left:5px;
  grid-column:3;
  display:flex;
  align-items:center;
  font-size:18px;
}

.arrow {
  grid-column:2;
  font-size: 30px;
  display:absolute;
  justify-content:center; 
  align-items:center;
  position: absolute;
  top:25px;
  left:60px;
}

.arrow::after {
  content: '';
  display: block;
  width: 60px; /* Tail length */
  height: 2px;  /* Tail thickness */
  background-color: black; /* Tail color */
  position: absolute;
  top:21.5px;
  left:-35px;
}

.line-container {
  display: flex;
  align-items: center;
  margin: 12px 0 10px 0;
}



.half-circle {
  width: 20px;
  height: 20px;
  border-radius: 60%; /* Creates a circle */
  background-color: rgb(149, 147, 147);
  position: relative;
}

.left-circle::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 55%;
  left: 46%;
  top: 0;
  transform: translateX(-50%);
  overflow-x:hidden;
  overflow-y:hidden;
}

.right-circle::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 50%;
  right: 46%;
  top: 0;
  transform: translateX(50%);
}



.booking-details {
  display: grid;
  grid-template-columns: repeat(2,1fr);
}

.booking {
  grid-column: 1;
  box-sizing:border-box;
  margin-left: 25px;
}

.validity {
  grid-column: 2;
  margin-left: 10px;
}

.time {
  font-size: 13px;
  font-weight: 600;
  margin: 5px 0;
  color:rgb(25, 25, 25);
}

.prime-code {
  font-size: 15px;
  text-align:center;
  color:rgb(25, 25, 25);
  font-weight: 400;
}

.image {
  width: 100px;
  height: 70px;
  /* background-color:#da0922; */
  margin: 0 auto;
  margin-top: 20px;
  
  /* margin-bottom: 30px; */
  
}

.logo {
  object-fit:cover;
  margin-top:30px;
  width: 100%;
  
}


/* animation */

.pulsate-fwd {
	-webkit-animation: pulsate-fwd 1.5s ease-in 1s infinite forwards;
	        animation: pulsate-fwd 1.5s ease-in 1s infinite forwards;
}

 @-webkit-keyframes pulsate-fwd {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes pulsate-fwd {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



/* input form */

.inputs {
  margin:10px auto;
}
